import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators, NgForm } from '@angular/forms';
import { MemberService } from '../../services/member.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-sing-in',
  templateUrl: './sing-in.component.html',
  styleUrls: ['./sing-in.component.css']
})
export class SingInComponent implements OnInit {

  myGroup:FormGroup;

  constructor(private formBulid:FormBuilder,private dataService:MemberService,private router:Router) { }

  ngOnInit() {
    this.myGroup = this.formBulid.group({
      email : ['',[Validators.required,Validators.minLength(1),Validators.email]],
      password : ['',Validators.required]

    });

    console.log(window.localStorage);
  }

  postdata(angForm1:NgForm){
    //console.log(angForm1.value);
    let user_data = angForm1.value;
    this.dataService.userlogin(user_data);
  }
}
