import { Injectable } from '@angular/core';
import { HttpClient,HttpHeaders } from '@angular/common/http';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';


@Injectable({
  providedIn: 'root'
})
export class GroupMemberService {

  myHeaders = new HttpHeaders({
    "Content-Type": "application/x-www-form-urlencoded"
  });

  success_msg = '';
  err_msg = '';
  all_member;
  all_expense;

  deleteMem_msg;
  single_member = {};
  success_update;

  GetTotalAmount;


  constructor(private http:HttpClient,
    private route:Router,
    private toastr:ToastrService) { }

      //// member add service
     insert_member(post){
            console.log(post);
            let user_id = localStorage.getItem("nguserId");
            let post_data = "name="+post.name +
                     "&user_id="+user_id;
            this.http.post("http://localhost/expanse_management/member.php",
            post_data,
            {headers:this.myHeaders})
            .subscribe(
              res => {
                if( res != null &&
                  res["status"] ==1
                  ){
                    this.success_msg = res["msg"];
                    this.toastr.success('Add Member successfully');
                  }else{
                    this.toastr.info('Something went wrong.');
                  }
              },
              err => {
                console.log(err);
              }
            );
     }
     
  // get all member data 
  getMember(){
    let user_id = localStorage.getItem("nguserId");
    //return this.http.get("http://localhost/expanse_management/get_meber.php?user_id="+user_id)
    return this.http.get("http://localhost/expanse_management/get_meber.php")
.subscribe(
      res => {
        console.log(res);
        if (res != null) {
          this.all_member = res;
        }
      },
      err => {
        console.log(err);
      }
    );

  }

  // delete member services

  delete_Member(id,i){
    this.http.get("http://localhost/expanse_management/delete_member.php?id="+id)
    .subscribe(
      res => {
        if(res != null && res['status']==1){
            this.deleteMem_msg = res['msg'];
           
             this.all_member.splice(i,1);
             this.toastr.success('Member Delete successfully');
        }
      },
      err => {
        console.log(err);
      }
    )
 }

 // edit member data

 edit_member(id){

  this.http.get("http://localhost/expanse_management/get_single%20_member.php?id="+id)
  .subscribe(
    res => {
      if(res != null){
         this.single_member = res;
         console.log(this.single_member);
      }
    },
    err => {
      console.log(err);
    }
  )

 }

 //// update member 

 update_member(updataData){

  
  let update = "name="+updataData.name +
               "&id="+updataData.id;
               console.log(update);
    this.http.post("http://localhost/expanse_management/update_member.php",
    update,
    {headers:this.myHeaders})
    .subscribe(
      res => {
        console.log(res);
        if(res != null && res['status']==1){
         this.success_update = res['msg'];
         let index = this.all_member.indexOf(
         this.all_member.find(e => {
           updataData.id = e.id;
         })

         );
         this.all_member.splice(index,1,updataData);
         this.route.navigate(['/home/view-member']);
         this.toastr.success('Member Updated successfully');
         
        }
      },
      err => {
        console.log(err);
      }

    )
 }

  // insert expense data
  insertData(data){
    console.log(data);
    let user_id = localStorage.getItem("nguserId");
    let u_data = "amount="+data.amount +
                 "&description="+data.description +
                 "&given="+data.given +
                 "&user_id="+user_id;
      this.http.post("http://localhost/expanse_management/expense.php",
      u_data,
      {headers:this.myHeaders})
      .subscribe(
        res => {
          if(res != null && res["status"]==1){
            this.success_msg = res["msg"];
            this.toastr.success('successfully Added');
          }else{
            this.toastr.info('Something went wrong.');
          }
        },
        err =>{
          console.log(err);
        }
      ) 
  }
     // get Expense data service
     getTableData(){
      let user_id = localStorage.getItem('nguserId');
      return this.http.get("http://localhost/expanse_management/get_expense.php?user_id="+user_id)
      .subscribe(
        res => {
          if(res != null){
            this.all_expense = res;
          }
        },
        err => {
          console.log(err);
        }
      )
    }

    // get total amount 
    totalAmount(){
      let id = localStorage.getItem('nguserId');
      return this.http.get("http://localhost/expanse_management/total.php?id="+id)
      .subscribe(
        res => {
          if(res != null){
              this.GetTotalAmount = res;
              console.log(this.GetTotalAmount);
          }
        },
        err => {
          console.log(err);
        }
      )
    }
  
}