import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
const myheader = new HttpHeaders({'content-type' : 'application/x-www-form-urlencoded'});
@Injectable({
  providedIn: 'root'
})
export class MemberService { 
  signupmsg = "";
  loginmsg = "";
  userExist = false;
  constructor(private http:HttpClient,
    public router:Router,
    private toastr:ToastrService) { }
  
  ///// user registration service
  insert_user(userdata){
    console.log(userdata);
    let u_data = 
    "username="+userdata.username +
    "&email="+userdata.email +
    "&firstname="+userdata.firstname +
    "&lastname="+userdata.lastname +
    "&password="+userdata.password;
    this.http.post("http://localhost/expanse_management/user.php",
    u_data,
    {headers:myheader})
    .subscribe(
      res => {
        console.log(res);
        if(res != null && res["status"]==1){
          this.router.navigate(["/login"]);
        }else{
          this.toastr.info('Something went wrong.');
        }
        },
        err =>{
          console.log(err);
        }
      
    );
}
    /// user login check service
  public userlogin(data){
    let u_data = "email="+data.email +
       "&password="+data.password;
       this.http.post("http://localhost/expanse_management/login.php",
       u_data,
       {headers:myheader})
       .subscribe(
         res => {
           console.log(res); 
           if(res != null && res["status"]==1){
             this.userExist = true;
             localStorage.setItem("nguserId", res["userdata"].id);
             localStorage.setItem("nguserName",res["userdata"].firstname);
             this.router.navigate(["/home/add-member"]);
           }else if (res != null && res["status"] == 0) {
            this.loginmsg = res["msg"];
            this.toastr.error('Username Or Password Is Incorrect.');
          }else {
            this.toastr.info('Something went wrong.');
          }
         },
         err => {
           console.log(err);
         }
       )
  }
}
